* {
	padding: 0px;
	margin: 0px;
	border: 0px;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

:focus,
:active {
	outline: none;
}

a:focus,
a:active {
	outline: none;
}

aside,
nav,
footer,
header,
section {
	display: block;
}
.wrapper {
	min-height: 100%;
	// overflow: hidden; внимание опасная вещь, если раскоментить то сайт пойдет по левой дорожке
}
.container {
	max-width: 1067px;
	margin: 0 auto;
}
html,
body {
	height: 100%;
	min-width: 320px;
}

body {
	line-height: 1;
}

input,
button,
textarea {
}

input::-ms-clear {
	display: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
	appearance: none;
}

textarea {
	resize: none;
	padding: 0px 0px;
}

button {
	cursor: pointer;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

a,
a:visited {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
}
