// Подключение своих SCSS файлов
@import "reset";
@import "fonts";
@import "header";

.wrapper {
    min-height: 100%;
}
section {
    background-size: cover;
    background-repeat: no-repeat;
}

* {
    margin: 0;
    padding: 0;
}

.main__image {
    width: 100%;
    background-size: cover;
    position: sticky;
    top: 0;
    left: 0;
}

.about__company {
    height: 100vh;
    width: 100%;
    background: linear-gradient(180deg, #0e124e 0%, #0e134f 100%);
    position: sticky;
    top: 0;
    left: 0;
}

@keyframes text {
    0% {
        -webkit-background-clip: text;
        -webkit-text-fill-color: white;
        background-size: 0;
    }
    100% {
        background: linear-gradient(180deg, #fff 20%, red 200%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-size: 100%;
    }
}

.nav__item {
    & > a {
        // background: linear-gradient(135deg, #12bcb0 20%, #fa0e0e 70%);

        transition: background-size 1.5s ease-in-out;
        &:hover {
            animation-name: text;
            animation-duration: 2.5s;
            animation-fill-mode: forwards;
            animation-iteration-count: 1;

            // border: 3px solid transparent;
        }
    }
}

@import "footer";
@import "adaptive";
