.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__logo {
    }
}
.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 400px;
    width: 100%;
    &__item {
        a {
            color: #fff;
        }
    }
}
